.App {
  margin: 100px auto 0;
  width: 300px;
  height: 400px;
  
  text-align: center;
}

.App .login {
  margin-top: 20px;
}

.room {
  display: flex;
  flex: 1;
  justify-content: center;
}

.room .inner {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.room header, .room footer {
  color: #333;
  text-align: center;
  height: 60px;
  line-height: 60px;
  background: #ccc;
}

.room footer {
  padding: 0 20px;
  display: flex;
  align-items: center;
}

.room footer button {
  margin-left: 100px;
}

.room .content {
  display: flex;
  flex: 1;
  overflow: auto;
}

.room .content .user-list {
  margin: 0;
  width: 30%;
  background: rgb(233, 233, 233);
  overflow-y: auto;
}

.room .content .user-list .user-list-item {
  margin: 10px 20px;
  padding: 10px;
  border-radius: 5px;
  background: rgb(131, 177, 131);
}

.room .content .user-list .user-list-item span {
  margin-right: 8px;
}

.room .content .message-list {
  margin: 0;
  width: 70%;
  overflow-y: auto;
}

.room .content .message-list .message-list-item {
  margin: 20px;
  padding: 10px;
  text-align: left;
}

.room .content .message-list .message-list-item.me {
  text-align: right;
}

.room .content .message-list .message-list-item .message-content {
  padding: 10px;
  background: rgb(241, 140, 140);
  border-radius: 5px;
  margin: 0 10px;
}

.room .content .message-list .message-list-item.me .message-content {
  background: rgb(102, 202, 102);
}